.home {
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 0.2rem;
    width: 100%;
    height: 100%;

    h2 {
        color: rgb(255, 255, 255);
        font-size: max(2vw, 1rem);
        font-weight: 500;
        text-align: center;
        margin: auto;
    }

    .logo {
        max-width: 100%; // Ensure the image doesn't exceed its container width
        height: auto; // Maintain the aspect ratio of the image
      }
      
    i {
        color: rgb(5, 176, 219);
        font-size: max(2vw, 1rem);
        font-weight: 200;
        text-align: center;
        font-family: "cursive";
    }
    b{
        color: rgb(255, 255, 255);
        font-size: max(2vw, 1rem);

        text-align: center;
        font-family: "cursive";
        font-size: xx-large;
    }
    h5 {
        color: rgb(5, 176, 219);
        font-size: max(2vw, 1rem);
        font-weight: 200;
        text-align: center;
    }
    h3 {
        color: rgb(255, 255, 255);
        font-size: max(2vw, 1rem);
        font-weight: 500;
        text-align: center;
        margin: auto;
    }
    gif{
        grid-auto-flow: column;
        max-width: 100%; // Ensure the image doesn't exceed its container width
        height: auto;
    }
    h1 {
        color: rgb(5, 176, 219);
        font-size: max(1vw, 0.5rem);
        font-weight: 100;
        text-align: center;
    }

    .welcome {
        text-align: center;
        font-size: 48px;
      }
      
      .welcome h2 {
        position: relative;
        display: inline-block;
        perspective: 500px;
      }
      
      .welcome h2 span {
        position: relative;
        display: inline-block;
        transform-origin: bottom;
        transform: rotateX(90deg);
        opacity: 0;
        animation: rotateText 4s infinite alternate cubic-bezier(0.25, 0.1, 0.25, 1);
        color:  rgb(5, 176, 219); /* Change color here */
      }
      
      @keyframes rotateText {
        0% {
          transform: rotateX(90deg) translateY(50px);
          opacity: 0;
        }
        50% {
          transform: rotateX(0deg) translateY(0);
          opacity: 1;
        }
        100% {
          transform: rotateX(0deg) translateY(-50px);
          opacity: 0;
        }
      }        
}
